@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,500;0,600;1,100;1,400;1,600;1,800&family=Permanent+Marker&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-family: "Poppins", sans-serif;
}
body {
  width: 100%;
  background-color: #d0d3d4;
  height: 50vh;
}
.wrapper {
  margin: 10px auto;
  padding: 0 10%;
  padding-bottom: 10px;
  text-transform: capitalize;
}

.service-box {
  margin-top: 18rem;
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}
.box {
  padding: 20px;
  text-align: center;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
h1 {
  color: #f39c12;
  font-size: 3rem;
  text-align: center;
  padding-bottom: 15px;
  text-shadow: 0 15px 10px rgba(0, 0, 0, 0.2);
}
h1 span {
  color: #de3163;
  /* font-size: 3rem;
  text-align: center;
  padding-bottom: 15px;
  text-shadow: 0 15px 10px rgba(0, 0, 0, 0.2); */
}
.box:hover {
  color: #eee;
  transform: scale(1.03);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
}
@media (max-width: 770px) {
  .wrapper {
    padding: 20px;
  }
  .developer {
    display: none;
  }
  h1{
    font-size: 2rem;
    transition: all .3s;
  }
}

.developer {
  color: #575052;
  font-size: 2rem;
  margin: 8rem 0 0cm 18rem;
}
